<template>
  <div class="film-box" style="height:100%">
    <div class="content"  :style="{ minHeight: curHeight+'px'}" >
      <div class="ppp">
        <!-- 导航-->
        <div class="nav-box">
          <div class="nav-left" @click="toFront()"><img src="../../assets/image/market_details/back.png" alt="" style="width: 0.44rem;height: 0.42rem;"></div>
        <!-- <img src="../../assets/image/market_details/back.png" alt="" style="width: 0.44rem;height: 0.42rem;">-->
          <span class="nav-title">充值</span>
          <div class="nav-right" @click="toOrder()">历史记录<img src="../../assets/image/capital/record.png" alt="" style="width: 0.35rem;height: 0.39rem;"></div>
        </div>
        <!--选择币种 tab-->
        <!-- <div class="select-box">
          <div style="color: white;font-size: 0.32rem;">BNB</div>
          <div>选择币种 <van-icon name="arrow" /></div>
        </div> -->
        <!--中间区域 main-->
        <div class="middle-box">
  <!--        复制地址区域-->
          <div class="addr-box">
            <span>BNB充值地址</span>
            <span style="color: #0092FE" @click="copy(address)">复制地址</span>
          </div>
  <!--        时间区域-->
          <div class="time">2020.06.04 5555</div>
          <div style="margin-top: 0.36rem">{{wallet_address}}</div>
  <!--        二维码区域-->
          <div class="qr-code-box">
            <img :src="url+qrcod" alt="" class="qr-code-img">
            <p class="save-qrcode">保存二维码</p>
          </div>
  <!--        底部提示区域-->
          <div class="tips-box">
            <p class="tips-title">温馨提示：</p>
            <p style="margin-bottom: .17rem">此地址只可接收BNB</p>
            <p>*请不要向这个地址充值除BNB之外的资产，任何充入此地址的其 它资产将不可找回。</p>
          </div>
          <div class="hide-box"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { qrcoderecharge } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { Icon, Toast } from 'vant'

Vue.use(Icon).use(Toast)
export default {
  components: {},
  data () {
    return {
      url: this.GLOBAL.domainName,
      curHeight: '',
      wallet_address: '',
      qrcod: '',
      qrcoderechargeData: {
        u_id: ''
      }
    }
  },
  created () {
    this.qrcoderechargeData.u_id = sessionStorage.getItem('u_id')
    this.beforeMount()
    this.addwithdrawFun()
  },
  methods: {
    toOrder () {
      this.$router.push({
        path: '/personal/order',
        query: {
        }
      })
    },
    addwithdrawFun () {
      var that = this
      var data = objKeySort(this.qrcoderechargeData)
      var token = sgin(data)
      this.qrcoderechargeData.token = token
      qrcoderecharge(this.qrcoderechargeData).then(res => {
        console.log(res, '二维码成功')
        if (res.code === 0) {
          that.wallet_address = res.data.wallet_address
          that.qrcod = res.data.qrcod
          delete that.qrcoderechargeData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete that.qrcoderechargeData.token
      })
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    copy (data) {
      var url = data
      var oInput = document.createElement('input')
      oInput.value = url
      document.body.appendChild(oInput)
      oInput.select() // 选择对象;
      document.execCommand('Copy') // 执行浏览器复制命令
      this.$toast({ message: '复制成功', duration: 1500 })
    },
    toFront () {
      console.log('111')
      history.go(-1)
    }
  }
}
</script>
